import React, {Component} from "react";
import PropTypes from 'prop-types';
import {integer, fraction} from "../../../../../magnum2/components/Utils";

const MonthlyPricesMobile = (props) => {

        let monthlyPrices = props.monthlyPrices;
        if (!monthlyPrices || monthlyPrices.length==0) {
            return null;
        }

        let firstMonthPrice = monthlyPrices[0];
        return (
            <span className="opl-price-v2" data-price="{firstMonthPrice.gross}">
                <span className="opl-price-v2__part">
                    <span className="opl-price-v2__whole">{integer(firstMonthPrice.gross)}</span>
                </span>
                <span className="opl-price-v2__part">
                    <span className="opl-price-v2__separator">,</span>
                    <span className="opl-price-v2__decimal">{fraction(firstMonthPrice.gross)}</span>
                    <span className="opl-price-v2__suffix">{firstMonthPrice.currency}/mies.</span>
                </span>
            </span>
        );

};

export default MonthlyPricesMobile;
